@import "./Variables";

@mixin text ($fontFamily, $fontSize, $fontWeight, $lineHeight){
  font-family: $fontFamily;
  font-size: $fontSize;
  font-weight: $fontWeight;
  line-height: $lineHeight;
  color: $textColor;
}

@mixin smallGreyText ($fontFamily, $fontSize, $fontWeight, $lineHeight){
  font-family: $fontFamily;
  font-size: $fontSize;
  font-weight: $fontWeight;
  line-height: $lineHeight;
  color: $textSecondColor;
}